export default {
  name: "ProjectMain",
  data: () => ({
    projects: [],
    error: null,
  }),

  created() {
    this.$axios.get('https://api.aurelienk.space/projects/get.php').then(result => {
      if (result.data) this.projects = result.data;
    }).catch(error => {
      console.error(error);
      this.error = error;
    })
  }
}
