export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Projects",

  data: () => ({
    projects: [],
    error: null,
  }),
  head() {
    return {
      title: "Projets"
    };
  },

  created() {
    this.$axios.get('https://api.aurelienk.space/projects/get.php').then(result => {
      if(result.data) this.projects = result.data;
    }).catch(error => {
      console.error(error);
      this.error = error;
    })
  }
}
